import React from "react";
import { useParams } from "react-router-dom";

const IndividualTutorPage = () => {
  
  const { tutorName } = useParams();

  return (
    <>
      <div>
        <h1>
          {tutorName} Tutor
        </h1>
      </div>
    </>
  );
};

export default IndividualTutorPage;